import {Component, Input} from "@angular/core";
import 'rxjs/add/operator/switchMap';
@Component({
  selector : 'display-singlecode',
  templateUrl : './display-singlecode.component.html',
  styleUrls : ['./display-singlecode.component.css']
})
export class DisplaySingleCodeComponent {
  _elm : any;
  _from : string;

  constructor(){}
  ngOnInit(){

  }

  @Input() set elm(obj){
    this._elm = obj;
  }

  get elm(){
    return this._elm;
  }

  @Input() set from(obj){
    this._from = obj;
  }

  get from(){
    return this._from;
  }
}



